import React from "react";
import ReactDOM from "react-dom";
import "./style.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      checked: false
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    //;
    this.setState(
      {
        checked: e.target.checked
      },
      this.addExternalListener
    );
  }

  handleClick(param) {
    return (ev) => {
      ev.preventDefault();
      alert(`Test Param ${param}`);
    };
  }

  addExternalListener = () => {
    this.state.checked &&
      document.querySelectorAll(`a.demo-js`).forEach((a) => {
        a.addEventListener("click", this.handleClick("ciccio"));
      });
  };

  componentDidMount() {
    this.addExternalListener();
  }

  componentWillUnmount() {
    document.querySelectorAll(`a.demo-js`).forEach((a) => {
      a.removeEventListener("click", this.handleClick("ciccio"), false);
    });
  }

  render() {
    return (
      <div>
        <label htmlFor="name">Nom</label>
        <input
          type="checkbox"
          id="name"
          name="name"
          checked={this.state.checked}
          onChange={this.handleChange}
        />
        {this.state.checked && (
          <p>
            La case est coch√©e{" "}
            <a class="demo-js" href="#">
              CLICCA QUI
            </a>
          </p>
        )}
      </div>
    );
  }
}

ReactDOM.render(<App />, document.querySelector("#app"));
